.test-page,
.chat-page{
  @media screen and (min-width: 993px){
    .header__inner{
      display: none;
    }
  }
}

.test{
  flex-grow: 1;
  position: relative;
  padding: 7.5em 0;
  &__inner{
    max-width: 58.1875em;
    padding: 2.5em;
    position: static;
  }
  &__top{
    .text40{
      max-width: 18.4em;
    }
  }
}

.test-progress{
  margin-top: 2.5em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .progressbar{
    margin-top: 1em;
  }
}

.progressbar{
  height: 2em;
  border-radius: 6.25em;
  position: relative;
  width: 100%;
  background-color: $purple2;
  span{
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    border-radius: inherit;
    background: linear-gradient(90deg, #FFC208 0%, #FF8E75 100%);
  }
}

.test-question{
  position: absolute;
  right: 0;
  top: 0;
  min-height: 100%;
  width: 47.375em;
  border-radius: 1.5em;
  background: rgba(255, 255, 255, 0.40);
  backdrop-filter: blur(10px);
  padding: 2.5em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__text{
    margin-top: 2.5em;
  }
  &__img{
    margin-top: 2.5em;
    display: block;
    border-radius: 2.5em;
    width: 100%;
  }
}

.test-answers{
  margin-top: 2.5em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1.25em));
  gap: 2.5em;
}

.answer{
  position: relative;
  input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    cursor: pointer;
    opacity: 0;
    &:checked+.answer__inner{
      background-color: $white100;
      border-color: $purple2;
    }
  }
  &__inner{
    padding: 1em;
    border-radius: 1.5em;
    background-color: #F1E4F8;
    min-height: 100%;
    border: 2px solid #F1E4F8;
  }
  &__text{
    margin-top: .5em;
  }
}


@media screen and (max-width: 992px){
  .test{
    padding: 1.5em 0;
    &__inner{
      border: none;
      padding: 0;
      background: unset;
      backdrop-filter: unset;
      border-radius: 0;
    }
    &__top,
    &__bottom{
      border-radius: 1.5em;
      background: rgba(255, 255, 255, 0.40);
      backdrop-filter: blur(10px);
      border: 1px solid $white100;
      padding: 1.5em;
    }
    &__top{
      .text40{
        font-size: 1em;
        max-width: 70%;
      }
    }
    &__bottom{
      margin-top: 2.5em;
    }
  }

  .test-progress{
    margin-top: .875em;
    .progressbar{
      margin-top: .875em;
    }
    &__title{
      font-weight: 500;
      font-family: $font1;
    }
  }

  .progressbar{
    height: .75em;
  }

  .test-question{
    position: static;
    width: 100%;
    background: transparent;
    backdrop-filter: unset;
    padding: 0;
    border-radius: 0;
    &__top{
      padding-bottom: .875em;
      border-bottom: 1px solid $purple2;
      .text40{
        font-size: 1em;
      }
      .text24{
        font-size: .625em;
      }
    }
    &__text{
      margin-top: .875em;
      .text40{
        font-size: 1em;
      }
    }
    &__img{
      margin-top: 1.5em;
    }
  }

  .test-answers{
    margin-top: 2em;
    grid-template-columns: repeat(2,calc(50% - .25em));
    gap: .5em;
  }

  .answer{
    &__inner{
      border-radius: 1em;
    }
    .text20{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .test-answers{
    grid-template-columns: 100%;
  }
}
