.breadcrumbs{
    display: flex;
    margin-bottom: 2.5em;
    &__wrap{
        display: flex;
        padding: .25em 1em;
        border-radius: 5em;
        background: #F1E4F8;
        border: 1px solid $white100;
    }
    &__item{
        margin: .25em 0;
        padding: .25em .5em;
        border-radius: 2.5em;
        display: flex;
        font-weight: 500;
        position: relative;
        span{
            font-size: .625em;
        }
        &:not(:last-child){
            margin-right: 2em;
            &:after{
                content: '';
                position: absolute;
                left: 100%;
                top: 50%;
                transform: translateY(-50%);
                margin-left: .25em;
                background-image: url(../img/bread-arrow.svg);
                @include bgImgSize(100%);
                width: 1.5em;
                height: 1.5em;
            }
        }
        &:last-child{
            color: $purple2;
            background-color: $white100;
        }
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        display: none;
    }
}
