.header{
  &__inner{
    padding: 4.5em 0 2.5em;
    display: grid;
    grid-template-columns: calc(50% - 9em) 16em calc(50% - 9em);
    gap: 1em;
  }
  &__left{
    display: flex;
  }
  &__logo{
    display: block;
    width: 100%;
    img{
      width: 100%;
      display: block;
    }
  }
  &__right{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__btn{
    margin-left: 1em;
    flex-shrink: 0;
  }
  &__mob{
    &-buttons{
      display: none;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  padding: 1.25em 1.5em;
  backdrop-filter: blur(10px);
  border-radius: 1em;
  background-color: $primary;
  color: #fff;
  &__link{
    display: flex;
    align-items: center;
    font-weight: 500;
    @include lastM(0 1em 0 0);
    svg{
      width: 1.5em;
      height: auto;
      margin-right: .5em;
      flex-shrink: 0;
    }
    span{
      font-size: 1em;
      line-height: 138%;
    }
    &:hover{
      color: $yellow;
      path{
        stroke: $yellow;
      }
    }
  }
}

.logo{
  @include center;
  height: 4.5em;
  border-radius: 1em;
  background: rgba(255, 255, 255, 0.40);
  backdrop-filter: blur(10px);
  font-family: $font2;
  color: $black300;
  img{
    width: 3.4375em;
    flex-shrink: 0;
    margin-right: 1.5em;
  }
  span{
    font-size: 1.5em;
  }
}

.header-notification{
  margin-left: 1em;
  flex-shrink: 0;
  &__btn{
    position: relative;
  }
  &__pin{
    position: absolute;
    font-size: 1em;
    right: 1.3125em;
    top: 0.9375em;
    width: 0.5em;
    height: 0.5em;
    border-radius: 50%;
    background-color: $yellow;
  }
}

.header-search{
  width: 16.1875em;
}

.search-field{
  position: relative;
  &__icon{
    position: absolute;
    left: 1.5em;
    top: 50%;
    transform: translateY(-50%);
    width: 1.5em;
    height: auto;
  }
  input{
    font-size: 1em;
    padding: 0 1em 0 4.31em;
    width: 100%;
    border-radius: 1em;
    background: rgba(255, 255, 255, .4);
    backdrop-filter: blur(10px);
    border: 1px solid #fff;
    height: 4em;
    font-weight: 500;
    color: $black200;
    &::placeholder{
      color: $black200;
    }
    &:focus{
      background: rgba(255, 255, 255, .7);
    }
  }
}

.header-user{
  display: none;
}

.avatar{
  width: 2.5em;
  height: 2.5em;
  display: block;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    border-radius: 50%;
    border: 2px solid #795AA0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
  }
  img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
    display: block;
  }
}

.menu{
  position: fixed;
  left: 0;
  top: 50%;
  height: 100%;
  max-height: calc(100vh - 9em);
  transform: translateY(-50%);
  padding: 5.5em 1.75em 1.75em;
  border-radius: 0 1em 1em 0;
  background-color: rgba($white100,.7);
  z-index: 20;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__buttons{
    margin-top: 1.5em;
  }
  &__close{
    display: none;
  }
  &__btn{
    border: none;
    width: 2.5em;
    height: 2.5em;
    border-radius: 0.5em;
    svg{
      width: 1.5em;
      height: auto;
    }
  }
  &__links{
    margin: 3em 0 2em;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__link,
  &__logout{
    &:hover{
      color: $yellow;
      path{
        stroke: $yellow;
      }
    }
    span{
      display: none;
    }
  }
  &__link{
    @include lastM(0 0 1.5em);
    svg{
      width: 1.5em;
      height: auto;
    }
  }
  &__logout{
    padding: 1em .625em;
    width: 2.5em;
    margin-top: auto;
    display: block;
    svg{
      width: 100%;
      height: auto;
    }
  }
}

@media screen and (min-width: 993px){
  @media screen and (max-height: 1200px){
    .menu{
      max-height: calc(100vh - 3em);
    }
  }
  @media screen and (max-height: 1100px){
    .menu{
      padding: 1.75em;
      &__links{
        margin: 2em 0;
      }
    }
  }
  @media screen and (max-height: 1000px){
    .menu{
      &__link{
        @include lastM(0 0 1em);
      }
    }
  }
  @media screen and (max-height: 850px){
    .menu{
      &__links{
        margin: 1em 0;
      }
      &__link{
        svg{
          width: 1.25em;
        }
      }
    }
  }
  @media screen and (max-height: 750px){
    .menu{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &__inner{
      border-radius: 0 0 1em 1em;
      border: 1px solid $white100;
      background: rgba(255, 255, 255, 0.40);
      backdrop-filter: blur(10px);
      padding: .75em;
      grid-template-columns: calc(50% - 4.5em) 8.875em calc(50% - 4.5em);
      gap: 0;
      justify-content: space-between;
    }
    &__btn{
      display: none;
    }
    &__mob{
      &-buttons{
        display: flex;
      }
      &-btn{
        width: 2.25em;
        height: 2.25em;
        border-radius: .5em;
        margin-left: .5em;
        svg{
          width: 1.5em;
        }
      }
    }
  }

  .header-search,
  .header-notification,
  .nav{
    display: none;
  }

  .logo{
    padding: 0 .75em;
    height: 2.25em;
    border-radius: .5em;
    img{
      width: 1.75em;
      margin-right: .25em;
    }
    span{
      font-size: 1em;
    }
  }

  .header-user{
    display: block;
  }

  .avatar{
    width: 2.25em;
    height: 2.25em;
  }

  .burger{
    position: relative;
    &__pin{
      position: absolute;
      width: .5em;
      height: .5em;
      background: linear-gradient(90deg, #FFC208 0%, #FF8E75 100%);
      border-radius: 50%;
      top: .44em;
      right: .44em;
    }
  }

  .menu{
    border-radius: 0 0 1em 1em;
    top: -200%;
    left: 50%;
    transition: all .5s;
    transform: translateX(-50%);
    width: calc(100% - 2em);
    height: calc(100 * var(--vh) - 1em);
    max-height: unset;
    overflow-y: auto;
    background-color: #836dfe;
    padding: .75em .75em 1.75em;
    align-items: unset;
    &__top{
      background-color: inherit;
      z-index: 3;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      top: 0;
    }
    &__buttons{
      margin: 0;
      display: flex;
    }
    &__btn{
      width: 2.25em;
      height: 2.25em;
      background-color: transparent;
      border: 1px solid #fff;
      path{
        stroke: #fff;
      }
    }
    &__close{
      display: flex;
      margin-left: .5em;
    }
    &__links{
      width: 100%;
      align-items: flex-start;
      margin-top: 1.5em;
      padding: 0 .25em;
    }
    &__logout,
    &__link{
      color: #fff;
      display: flex;
      align-items: center;
      @include lastM(0 0 1em);
      svg{
        margin-right: .69em;
      }
      path{
        stroke: #fff;
      }
      span{
        display: block;
        font-size: 1em;
      }
    }
    &__logout{
      padding: 0 .625em;
      width: auto;
      height: auto;
      svg{
        width: 1.25em;
        margin-right: .81em;
      }
    }
  }

  .m-page{
    &._open{
      .menu{
        top: 0;
      }
    }
  }
}
