.chat{
  padding: 7.5em 0;
  &__inner{
    display: grid;
    grid-template-columns: 50% 50%;
    padding: 2em;
  }
}

.chat-list{
  max-width: 43em;
  .search-field{
    input{
      height: 5em;
      border: none;
      @media screen and (min-width: 993px){
        backdrop-filter: unset;
        background-color: $white100;
      }
    }
  }
}

.chat-items{
  margin-top: 2.5em;
  @include grid100gap(1.5em);
}

.chat-item,
.chat-user{
  .avatar{
    border-width: 3px;
    width: 5em;
    height: 5em;
  }
  &__img{
    flex-shrink: 0;
    margin-right: 1.5em;
  }
  &__info{
    @include grid100gap(.5em);
    padding-right: .5em;
  }
}

.chat-item{
  display: flex;
  align-items: center;
  padding: 1.5em;
  border-radius: 2.5em;
  background-color: $white100;
  border: 2px solid $purple2;
  &__count{
    flex-shrink: 0;
    margin-left: auto;
    height: 2em;
    min-width: 2em;
    padding: 0 .25em;
    @include center;
    color: #fff;
    background-color: $purple2;
    border-radius: 50%;
  }
}

.chat-area{
  padding-left: 7.5em;
  border-left: 1px solid $purple2;
  display: flex;
  flex-direction: column;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-shrink: 0;
  }
  &__content{
    flex-grow: 1;
    min-height: 29em;
    max-height: 38em;
    overflow-y: auto;
    padding: 3.5em 0;
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
  }
}



.chat-user{
  display: flex;
  align-items: center;
}

.chat-nav{
  display: flex;
  align-items: center;
  &__btn{
    width: 2.5em;
    margin-left: 1em;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
}

.chat-field{
  position: relative;
  input{
    font-size: 1.5em;
    width: 100%;
    height: 3.25em;
    border-radius: 4.17em;
    border: none;
    padding-left: 3.33em;
    padding-right: 5em;
  }
  &__buttons{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    &._nav1{
      left: 2.5em;
    }
    &._nav2{
      right: 2.5em;
    }
  }
  &__btn{
    width: 1.5em;
    flex-shrink: 0;
    @include lastM(0 1.5em 0 0);
    svg{
      width: 100%;
      height: auto;
    }
  }
}

.message{
  display: flex;
  margin: 1.5em 0;
  .avatar{
    width: 4em;
    height: 4em;
    margin-right: 1.5em;
    flex-shrink: 0;
  }
  &__text{
    max-width: 29em;
    padding: 1.5em;
    border-radius: 0 1.5em 1.5em 1.5em;
    background: linear-gradient(90deg, #AA54D9 0%, rgba(86, 86, 86, 0.00) 100%);
    font-weight: 500;
    color: #fff;
  }
  &._type2{
    justify-content: flex-end;
    .message__text{
      flex-shrink: 0;
      border-radius: 1.5em 0 1.5em 1.5em;
      background: linear-gradient(90deg, #5D3EFE 0%, rgba(86, 86, 86, 0.00) 100%);
    }
  }
}


@media screen and (max-width: 992px){
  .chat{
    padding: 1.5em 0 4.75em;
    &__inner{
      position: relative;
      grid-template-columns: 100%;
      @include unsetBackdrop;
    }
    &._open-message{
      .chat-list{
        opacity: 0;
        pointer-events: none;
      }
      .chat-area{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .chat-list{
    transition: all .3s;
    max-width: 100%;
    .search-field{
      input{
        border: 1px solid $white100;
        height: 3em;
        border-radius: 1.5em;
        padding-left: 2.13em;
      }
      &__icon{
        left: 1em;
        width: .75em;
      }
    }
    &__mob{
      @include setBackdrop;
      padding: 2em 1.5em;
      margin-top: 1.125em;
    }
    .profile__mob-title{
      margin: 0;
    }
  }

  .chat-items{
    gap: 0;
    margin-top: .125em;
  }

  .chat-item,
  .chat-user{
    .avatar{
      width: 1.875em;
      height: 1.875em;
      img{
        border-width: 2px;
      }
    }
    &__img{
      margin-right: .75em;
    }
    &__info{
      gap: .125em;
    }
  }

  .chat-item{
    padding: 1em 0;
    border: none;
    border-bottom: 1px solid rgba($purple2,.3);
    background-color: transparent;
    border-radius: 0;
    &:last-child{
      border-bottom: none;
      padding-bottom: 0;
    }
    &__count{
      min-width: 1.5em;
      height: 1.5em;
    }
  }

  .chat-area{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 2;
    opacity: 0;
    padding: 0 0 4.5em;
    border: none;
    &__content{
      max-height: 29em;
      padding: .75em 0;
    }
    &__top{
      padding-bottom: 1em;
      border-bottom: 1px solid rgba($primary,.3);
    }
  }

  .chat-nav{
    &__btn{
      width: 1.5em;
      margin-left: .5em;
      path{
        stroke: $black200;
      }
    }
  }

  .message{
    margin: 1em 0;
    .avatar{
      margin-right: 1em;
      width: 2.5em;
      height: 2.5em;
    }
    &__text{
      padding: .75em 1em;
      max-width: 18em;
      border-radius: 0 1em 1em 1em;
    }
    &._type2{
      .message__text{
        border-radius: 1em 0 1em 1em;
      }
    }
  }

  .chat-field{
    input{
      font-size: 1em;
      padding-left: 2.38em;
      height: 3.75em;
      border-radius: 6.25em;
    }
    &__buttons{
      &._nav1{
        left: 1em;
        .chat-field__btn{
          width: 1em;
        }
      }
      &._nav2{
        right: 1em;
      }
    }
    &__btn{
      @include lastM(0 .5em 0 0);
      path{
        stroke: $black200;
      }
    }
  }
}
