.auth-page{
  @media screen and (min-width: 993px){
    .container{
      padding-left: 4.5em;
    }
  }
  .burger,
  .menu{
    display: none;
  }
}


.auth{
  padding: 4em 0;
  &__inner{
    max-width: 100em;
    padding: 2.5em;
    border-radius: 1.5em;
    background: rgba(255, 255, 255, 0.40);
    backdrop-filter: blur(10px);
  }
  &__top{
    @include grid100gap(1em);
  }
}

.auth-form{
  margin-top: 2.5em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    gap: 2.5em;
  }
  &__nav{
    margin-top: 2.5em;
  }
  &__btn{
    width: 16.25em;
    max-width: 100%;
  }
}

.auth-code{
  &__wrap{
    display: flex;
    margin-top: 1.5em;
  }
  &__btn{
    margin-left: .75em;
    font-weight: 500;
    &:hover{
      span{
        text-decoration-color: transparent;
      }
    }
    span{
      transition: inherit;
      text-decoration: underline;
    }
  }
}


@media screen and (max-width: 992px){
  .auth{
    padding: 1.5em 0;
    &__inner{
      padding: 3em 1.5em;
    }
    &__top{
      gap: .5em;
    }
  }

  .auth-form{
    margin-top: 7em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
    &__nav{
      margin-top: 2em;
    }
    &__btn{
      width: 10.4375em;
    }
  }
}


@media screen and (max-width: 576px){
  .auth-form{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
