.profile,
.profile .container{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.profile{
  padding: 4em 0;
  &__inner{
    flex-grow: 1;
    padding: 2.5em;
    border-radius: 1.5em;
    background: rgba(255, 255, 255, 0.40);
    backdrop-filter: blur(10px);
    display: grid;
    grid-template-columns: 22em calc(100% - 29.5em);
    gap: 7.5em;
  }
  &__sidebar{
    padding-right: 2.5em;
    border-right: 1px solid $purple2;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__save{
    @media screen and (min-width: 993px){
      padding: 0 1em;
      height: 2.25em;
      border-radius: 6.25em;
      font-weight: 400;
      span{
        font-size: 1em;
      }
    }
  }
  &__mob-title{
    display: none;
  }
}

.profile-form{
  margin-top: 1.5em;
  max-width: 61.5em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
  }
}

.profile-nav{
  display: flex;
  flex-direction: column;
  max-width: 15em;
  &__link{
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include lastM(0 0 2.5em);
    svg{
      width: 1.5em;
      height: auto;
      flex-shrink: 0;
    }
    &:hover{
      color: $purple2;
    }
  }
}


@media screen and (max-width: 992px){
  .profile{
    padding: 1.5em 0;
    &__inner{
      flex-grow: unset;
      padding: 1.5em 1.5em 7.75em;
      grid-template-columns: 100%;
      position: relative;
    }
    &__sidebar{
      display: none;
    }
    &__save{
      position: absolute;
      bottom: 1.5em;
      left: 1.5em;
    }
    &__mob-title{
      display: block;
      padding-bottom: .875em;
      border-bottom: 1px solid $purple2;
      margin-bottom: .875em;
    }
    .text40{
      font-size: 1em;
    }
  }

  .profile-form{
    margin-top: 2em;
  }
}


@media screen and (max-width: 576px){
  .profile-form{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
