.confirm{
  padding-bottom: 4.5em;
  &__inner{
    @include setBackdrop;
    padding: 2.5em;
  }
  .text{
    max-width: 86em;
    margin-top: 2.5em;
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
  }
  &__btn{
    width: 13.3125em;
    height: 3.25em;
    border-radius: .375em;
    span{
      font-size: 1em;
    }
  }
}

.text{
  p{
    font-size: 1em;
    line-height: 150%;
  }
  h3{
    @include lastM(0 0 .67em);
  }
  p{
    @include lastM(0 0 1em);
  }
  hr{
    border: none;
    display: block;
    height: 1.5em;
  }
}


@media screen and (max-width: 992px){
  .confirm{
    padding: 2.5em 0;
    &__inner{
      padding: 1.5em;
    }
    &__title{
      padding-bottom: 1.5em;
      font-size: 1em;
      border-bottom: 1px solid $purple2;
    }
    &__nav,
    .text{
      margin-top: 1.5em;
    }
    &__btn{
      width: 10.3125em;
      height: 3.75em;
      border-radius: 1em;
    }
  }

  .text{
    p{
      font-size: .875em;
    }
    h3{
      @include lastM(0 0 1em);
    }
    hr{
      height: 1em;
    }
  }
}
