.stat{
  padding-bottom: 4em;
  &__inner{
    display: grid;
    grid-template-columns: 25.125em calc(100% - 32em);
    justify-content: space-between;
  }
  ._success{
    color: #02A58B;
  }
  ._danger{
    color: $red;
  }
}

.stat-rating{
  padding-bottom: 100%;
  position: relative;
  background: rgba(255, 255, 255, 0.40);
  backdrop-filter: blur(10px);
  border-radius: 50%;
  &__inner{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @include center;
    flex-direction: column;
  }
  &__title{
    font-size: 1.5em;
    line-height: 130%;
    margin-bottom: .5em;
    font-family: $font2;
    color: $purple2;
  }
  &__num{
    font-size: 3.5em;
    font-family: $font2;
    @media screen and (min-width: 993px){
      background: linear-gradient(90deg, #FFC208 0%, #FF8E75 100%);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }
}

.stat-history{
  margin-top: 4.94em;
  font-family: $font2;
  &__title{
    padding-bottom: .625em;
    border-bottom: 1px solid $purple2;
  }
  &__item{
    padding: .25em 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid $purple2;
    &-title{
      font-size: .8125em;
      line-height: 108%;
    }
    &-desc{
      font-size: 1.125em;
      line-height: 111%;
    }
  }
}

.stat-table{
  padding: 1.875em 1.75em 1.25em;
  @include setBackdrop;
  font-family: $font2;
  table{
    width: 100%;
    border-collapse: collapse;
  }
  th,
  td{
    padding: .5em 0;
    text-align: left;
    border-bottom: 1px solid $purple2;
    &:not(:first-child){
      padding-left: 1em;
    }
    &:nth-child(2){
      width: 14em;
    }
  }
  th{
    white-space: nowrap;
    span{
      font-size: 1.25em;
      line-height: 120%;
    }
  }
  td{
    &>span{
      font-size: 1em;
      line-height: 125%;
    }
  }
  &__rating{
    display: flex;
    &-num{
      font-size: 1.25em;
      line-height: 120%;
    }
    &-desc{
      margin-left: .25em;
      padding-top: .3125em;
      font-size: 1em;
      line-height: 125%;
      font-weight: 500;
      font-family: $font1;
    }
  }
}


@media screen and (max-width: 992px){
  .stat{
    padding-top: 2.5em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__content{
      display: none;
    }
    &__top{
      max-width: 23em;
      margin: 0 auto;
    }
  }

  .stat-rating{
    border: 1px solid $white100;
    &__title{
      color: $black200;
      margin-bottom: .4em;
    }
  }

  .stat-history{
    margin-top: 2.5em;
    @include setBackdrop;
    padding: 1.5em;
  }
}
