@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.m-page {
    background-color: #DCDBFF;
    background-image: url(../img/bg.svg);
    background-size: 100% auto;
    background-repeat: repeat-y;
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: $black200;
    font-size: calc(1920px * (16 / 1920));
    @media screen and (max-width: 1920px){
        font-size: calc(100vw * (16 / 1920));
    }
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
    }
}

.container {
    max-width: 1920px;
    width: 100%;
    margin: 0 auto;
    padding: 0 4.5em 0 10em;
}

a {
    &:hover {
        color: $purple2;
    }
}


@media screen and (max-width: 992px){
    .m-page{
        font-size: calc(100vw * (16 / 768));
    }

    .container{
        padding: 0 1em;
    }
}


@media screen and (max-width: 576px){
    .m-page{
        font-size: calc(100vw * (16 / 392));
    }
}
