.text40{
  font-size: 2.5em;
  line-height: 127.5%;
}

.text32{
  font-size: 2em;
  line-height: 128%;
}

.text h3,
.profile-nav__link,
.fg label,
.text24{
  font-size: 1.5em;
  line-height: 130%;
}

.text20{
  font-size: 1.25em;
  line-height: 120%;
}

.text16{
  font-size: 1em;
  line-height: 125%;
}

.text12{
  font-size: .75em;
  line-height: 125%;
}

.text h3,
.profile-nav__link,
.font2{
  font-family: $font2;
}

.primary-text{
  color: $primary;
}

.purple-text2{
  color: $purple2;
}


@media screen and (max-width: 992px){
  .text40{
    font-size: 1.5em;
  }

  .text h3,
  .fg label,
  .text24{
    font-size: 1em;
    line-height: 150%;
  }

  .text20{
    font-size: 1em;
    line-height: 125%;
  }

  .text14-mob{
    font-size: .875em;
  }

  .text12-mob{
    font-size: .75em;
  }

  .text10-mob{
    font-size: .625em;
  }
}
