.fg {
  position: relative;
  margin: 0;
  label{
    font-family: $font2;
    display: block;
    margin-bottom: .67em;
  }
  input {
    height: 4em;
    border: none;
    padding: 0 1.5em;
    width: 100%;
    font-size: 1em;
    border-radius: .5em;
    background-color: $white100;
    color: $black200;
    &:focus {
      background-color: #fff;
    }
  }
  &._required{
    label:after{
      content: '*';
      color: $red;
    }
  }
}

.pass-toggle{
  position: absolute;
  right: 1.5em;
  bottom: 1.5em;
  height: 1em;
  svg{
    height: 100%;
    width: auto;
  }
  &:hover{
    path{
      fill: $primary;
    }
  }
}

.date-field{
  &__icon{
    height: 1.5em;
    width: auto;
    bottom: 1.25em;
    right: 1.5em;
    position: absolute;
    pointer-events: none;
  }
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}


@media screen and (max-width: 992px){
  .fg{
    label{
      margin-bottom: .75em;
    }
    input{
      height: 3.25em;
      padding: 0 1em;
    }
  }

  .pass-toggle{
    bottom: 1.125em;
    right: 1em;
  }
}
